<template>
    <div>
        <h1>Login</h1>
        <form>
            <label for="name"></label>
            <input type="text" name="name" id="name" v-model="name" placeholder="name">
            <label for="email"></label>
            <input type="text" name="email" id="email" v-model="email" placeholder="email">
            <label for="password"></label>
            <input type="text" name="password" id="password" v-model="password" placeholder="password">
            <input type="submit" v-on:click="createUser">
        </form>
        <div class="users-container">
            <div
                v-for="(user, index) in users"
                v-bind:index="index"
                v-bind:item="user"
                v-bind:key="user._id">
                <p> {{ user }} </p>
            </div>
        </div>
    </div>
</template>

<script>
import UserService from "../UserService";

export default {
  name: 'LoginComponent',
  props: {},
  data() {
    return {
      users: [],
      error: "",
      name: "",
      password: "",
      email: ""
    }
  },
  async created() {
    try {
      this.users = await UserService.getUsers();
    } catch (err) {
      this.err = err.message;
    }
  },
  methods: {
    async createUser() {
      await UserService.insertUser(this.name, this.password, this.email);
      console.log("Hello world");
      this.users = await UserService.getUsers();
    },
    async deleteUser(id) {
      await UserService.deleteUser(id);
      this.users = await UserService.getUsers();
    }
  }
}
</script>

