import axios from "axios";

const url = "api/users";

class UserService {

    // GET users
    static getUsers() {
        return new Promise ((resolve, reject) => {
            axios.get(url).then((res) => {
                const data = res.data;
                resolve(
                    data.map(user => ({
                        ...user,
                        createdAt: new Date(user.createdAt)
                    }))
                );
            })
            .catch((err)=> {
                reject(err);
            })
        });
    }

    //CREATE user
    static insertUser(name, password, email) {
        return axios.post(url, {
            name: name,
            password: password,
            email: email
        })
    }

    //DELETE user
    static deleteUser(id) {
        return axios.delete(`${url}/${id}`)
    }
}

export default UserService